#footer {
    width: 100%;
    height: 100vh;
    background: url(../../public/img/bg.jpg) no-repeat center center /cover;
    overflow: hidden;
    position: relative;

    .footerImg {
        .text_area{
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-40%);
            display: flex;
            align-items: center;
            .text{
                color: #F17413;
                font-size: 1.2rem;
                font-weight: 800;
            }
        }
        .text_area_ko{
            position: absolute;
            top: 52%;
            left: 50%;
            transform: translate(-50%,-10%);
            display: flex;
            align-items: center;
              .ko{
                color: #fff;
                font-size: 2rem;
                font-weight: 100;
            }
        }

    }
}

@media screen and (max-width:1050px){
    #footer {

    }
}