#about {
  width: 100%;
  height: 100vh;
  background: #f8f8f8;
}
#about .info_area {
  width: auto;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
#about .info_area .infoImg {
  width: auto;
  height: 100%;
  display: block;
  padding-top: 80px;
}
#about .info_area .infoImg_m {
  display: none;
}

@media screen and (max-width: 1320px) {
  #about .container {
    padding: 0;
  }
  #about .info_area .infoImg {
    display: none;
  }
  #about .info_area .infoImg_m {
    display: block;
    width: auto;
    height: 100%;
    padding: 90px 0 30px;
  }
}/*# sourceMappingURL=about.css.map */