.project {
  width: 100%;
  height: 100vh;
  position: relative;
}
.project .img_area {
  width: auto;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  position: relative;
}
.project .img_area .projectImg {
  width: auto;
  height: 100%;
  display: block;
  padding-top: 80px;
}
.project .img_area .projectImg_m {
  display: none;
}
.project .img_area .btn {
  display: block;
  width: 140px;
  height: 140px;
  position: absolute;
  bottom: 5%;
  right: 5%;
  opacity: 0.8;
  transition: 0.2s ease-in-out;
}
.project .img_area .btn:hover {
  opacity: 1;
}
.project .img_area .btn.btn1 {
  background: url(../../public/img/btn.png) no-repeat center center/contain;
}
.project .img_area .btn.btn2 {
  background: url(../../public/img/btn2.png) no-repeat center center/contain;
}
.project .img_area .btn.btn3 {
  background: url(../../public/img/btn3.png) no-repeat center center/contain;
}

@media screen and (max-width: 1320px) {
  .project .container {
    padding: 0;
  }
  .project .img_area .projectImg {
    display: none;
  }
  .project .img_area .projectImg_m {
    display: block;
    width: auto;
    height: 100%;
    padding: 100px 0 20px;
  }
  .project .img_area .btn {
    width: 120px;
  }
}/*# sourceMappingURL=project.css.map */