#more {
  width: 100%;
  height: 100vh;
  background: url(../../public/img/more_bg.jpg) no-repeat center center/cover;
  overflow: hidden;
  position: relative;
}
#more .more_area {
  width: 930px;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -40%);
  z-index: 999;
}
#more .more_area .moreImg {
  width: 100%;
  display: block;
}
#more .more_area .moreBtn {
  display: block;
  width: 200px;
  height: auto;
  margin-top: 40px;
  opacity: 0.85;
  transition: 0.3s ease-in-out;
}
#more .more_area .moreBtn:hover {
  opacity: 1;
}
#more .more_area .moreBtn > img {
  width: 100%;
}

@media screen and (max-width: 880px) {
  #more .more_area {
    width: 100%;
  }
  #more .more_area .moreBtn {
    width: 180px;
  }
}/*# sourceMappingURL=more.css.map */