#about {
    width: 100%;
    height: 100vh;
    background: #f8f8f8;

    .info_area {
        width: auto;
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;

        .infoImg {
            width: auto;
            height: 100%;
            display: block;
            padding-top: 80px;
        }
        .infoImg_m {
            display: none;
        }
    }
}

@media screen and (max-width:1320px){
    #about {
        .container{padding: 0;}
        .info_area {
            .infoImg {
               display: none;
            }
            .infoImg_m {
                display: block;
                width: auto;
                height: 100%;
                padding: 90px 0 30px;
            }
    
        }
    }
}