#more {
    width: 100%;
    height: 100vh;
    background: url(../../public/img/more_bg.jpg) no-repeat center center /cover;
    overflow: hidden;
    position: relative;
.more_area{
    width: 930px;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-40%);
    z-index: 999;
    .moreImg{
        width: 100%;
        display: block;
    }
    .moreBtn{
        display: block;
        width: 200px;
        height: auto;
        margin-top: 40px;
        opacity: .85;
        transition: .3s ease-in-out;
        &:hover{opacity: 1;}
        >img{width: 100%;}
    }
}
    
}

@media screen and (max-width:880px){
    #more {
        .more_area{
            width: 100%;
            .moreBtn{width: 180px;}
        }
    }
}