#main {
    width: 100%;
    height: 100vh;
    background: url(../../public/img/bg.jpg) no-repeat center center /cover;
    overflow: hidden;
    position: relative;

    .mainImg {
        width: auto;
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;

        .uxuiHeaderImg {
            width: auto;
            height: 100%;
            display: block;
            padding-top: 70px;
        }
    }
    .pofo_info{
        width: 100%;
        position: absolute;
        bottom: 5%;
        display: flex;
        justify-content: space-between;
        color: #F17413;
        font-weight: 500;
        font-size: 1.15rem;
        padding: 0 5%;
    }
}

@media screen and (max-width:1050px){
    #main {
        .mainImg {
            .uxuiHeaderImg {
                width: 100%;
                height:auto;
            }
    
        }
    }
}