.project {
    width: 100%;
    height: 100vh;
    position: relative;

    .img_area {
        width: auto;
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;
        position: relative;

        .projectImg {
            width: auto;
            height: 100%;
            display: block;
            padding-top: 80px;
        }
        .projectImg_m {
           display: none;
        }
        .btn{
            display: block;
            width: 140px;
            height: 140px;
            position: absolute;
            bottom: 5%;
            right: 5%;
            opacity: .8;
            transition: .2s ease-in-out;
            &:hover{opacity: 1;}
            &.btn1{ background: url(../../public/img/btn.png) no-repeat center center /contain;}
            &.btn2{background: url(../../public/img/btn2.png) no-repeat center center /contain;}
            &.btn3{background: url(../../public/img/btn3.png) no-repeat center center /contain;}
        }
    }
}

@media screen and (max-width:1320px){
    .project {
        .container{padding: 0;}
        .img_area {
            .projectImg{display: none;}
            .projectImg_m {
                display: block;
                 width: auto;
                height: 100%;
                padding: 100px 0 20px;
            }
            .btn{
                width: 120px;
            }
        }
    }
}